<template>
    <!--自动站单站报表-->
  <div class="single-station-report-page">
    <div class="secondary-page-mode">
      <div class="secondary-toolbar">
        <span>数据类型：</span>
        <el-button-group>
          <el-button
            :class="{active:dataType === index}"
            size="small" type="primary" plain
            :key = "index"
            v-for="(item,index) in dataTypeList"
            @click="dataTypeToggle(index)"
          >{{ item.name }}</el-button>
        </el-button-group>&emsp;
        <span>时间段：</span>
        <div class="layoutBox" style="width:242px">
          <el-date-picker
            v-model = "dataValue"
            :type="headtimetype"
            placeholder="选择时间">
          </el-date-picker>
        </div>&emsp;
        <el-button @click="getTableData"><i class="iconfont icon-sousuo"></i>生成</el-button>
        <el-button type='delet'  @click="exportExcel"><i class="iconfont icon-daochu"></i>导出</el-button>
      </div>
      <div class="currency-mode-wrap" v-loading="loading"  element-loading-text="加载中"
               element-loading-spinner="loading-type-a"
               element-loading-background="rgba(255, 255, 255, 1)">

        <el-scrollbar class="currency-mode-scroll">
          <h4 class="report-form-title">{{headtitle}}</h4>
          <p class="report-form-subtitle">{{statisDate}}</p>
          <div class="report-table-wrap" style="position: relative;">
           
           
            <el-table
              :data="tableData"
              border
              ref="table"
              style="width: 100%">
              <el-table-column
                type="index"
                label="序号"
                fixed
                width="50">
              </el-table-column>
              <el-table-column
                prop="time"
                label="时间"
                fixed
                width="160">
              </el-table-column>
              <el-table-column
                prop="level"
                label="水质类别"
                fixed
                width="160">
              </el-table-column>
              <el-table-column :key="index" v-for="(item,index) in headcolumn"
                  :label="item.name">
                <el-table-column
                  :prop="item.code"
                  :label="item.unit">
                </el-table-column>
              </el-table-column>
            </el-table>
          </div>
        </el-scrollbar>
      </div>
    </div>
  </div>
</template>

<script>
import { formatDate } from "../../assets/js/dateformat";
import {exportTableData} from '../../assets/js/tableExport'
let dataTypeList = [
  {
    name: "日报",
    type: "date"
  },
  {
    name: "月报",
    type: "month"
  },
  {
    name: "年报",
    type: "year"
  }
];
let pickerOptions = {
  shortcuts: [
    {
      text: "最近一周",
      onClick(picker) {
        const end = new Date();
        const start = new Date();
        start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
        picker.$emit("pick", [start, end]);
      }
    },
    {
      text: "最近一个月",
      onClick(picker) {
        const end = new Date();
        const start = new Date();
        start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
        picker.$emit("pick", [start, end]);
      }
    },
    {
      text: "最近三个月",
      onClick(picker) {
        const end = new Date();
        const start = new Date();
        start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
        picker.$emit("pick", [start, end]);
      }
    }
  ]
};

export default {
  name: "singleStationReport",
  data() {
    return {
      reportLoading: true,
      reportNoDataState: false,
      dataTypeList, //数据类型
      dataType: 0, //数据类型选中下标
      pickerOptions, //时间快捷菜单数据
      dataValue: "", //时间绑定数据
      tableData: [], //表格数据,
      stationInfo: {},
      headcolumn: [],
      headtitle: "",
      statisDate: "",
      headtimetype: "date",
      loading: true,
      fieldList:[],
    };
  },
  methods: {
    dataTypeToggle(index) {
      this.dataType = index;
      if (index == 0) {
        this.headtimetype = "date";
      } else if (index == 1) {
        this.headtimetype = "month";
      } else if (index == 2) {
        this.headtimetype = "year";
      }
    },
    initTime() {
      if (this.headtimetype == "date") {
        this.dataValue = new Date().format("yyyy-MM-dd");
      } else if (this.headtimetype == "month") {
        this.dataValue = new Date().format("yyyy-MM");
      } else if (this.headtimetype == "year") {
        this.dataValue = new Date().format("yyyy");
      }
      console.log(this.dataValue);
    },
    initTitle() {
      if (this.headtimetype == "date") {
        this.headtitle = this.stationInfo.nodeName + "单站日报";
        this.statisDate = new Date(this.dataValue).format("yyyy-MM-dd");
      } else if (this.headtimetype == "month") {
        this.headtitle = this.stationInfo.nodeName + "单站月报";
        this.statisDate = new Date(this.dataValue).format("yyyy-MM");
      } else if (this.headtimetype == "year") {
        this.headtitle = this.stationInfo.nodeName + "单站年报";
        this.statisDate = new Date(this.dataValue).format("yyyy");
      }
    },
    //获取表格数据
    getTableData() {
      this.loading = true;
      let d = "";
      let timeType = "";
      if (this.headtimetype == "date") {
        d = new Date(this.dataValue).format("yyyy-MM-dd");
        timeType = "day";
      } else if (this.headtimetype == "month") {
        d = new Date(this.dataValue).format("yyyy-MM");
        timeType = "month";
      } else if (this.headtimetype == "year") {
        d = new Date(this.dataValue).format("yyyy");
        timeType = "year";
      }
      this.initTitle();
      this.$http
        .get("/StatisticsAndAlarm/dataReports/audioSingleReports", {
          params: {
            timeType: timeType,
            time: d,
            stationMn:  this.stationInfo.nodeId
          }
        })
        .then(res => {
          if (res.data.code == 0) {
            this.headcolumn = res.data.content.info;
            this.tableData = res.data.content.dataList;
          }
          this.loading = false;
        })
        .catch(res => {
          this.loading = false;
        });
    },
     //导出报表
    exportExcel(){
        let {table} = this.$refs;
        let tableBox = table.$el;
        let header = tableBox.querySelector('.el-table__header');
        let title = this.headtitle;
        let d = "";
        let timeType = "";
        if (this.headtimetype == "date") {
          d = new Date(this.dataValue).format("yyyy-MM-dd");
          timeType = "day";
        } else if (this.headtimetype == "month") {
          d = new Date(this.dataValue).format("yyyy-MM");
          timeType = "month";
        } else if (this.headtimetype == "year") {
          d = new Date(this.dataValue).format("yyyy");
          timeType = "year";
        }
        this.fieldList=["time","level"]
        for(let i =0;i<this.headcolumn.length;i++){
         
          this.fieldList.push(this.headcolumn[i].code)
         
        };
       
       
        let params = {
          headerProp:this.fieldList.join(','),
          timeType: timeType,
          time: d,
          stationMn:  this.stationInfo.nodeId
        };
        exportTableData(header,title,'','1',"/StatisticsAndAlarm/exports/singleWaterQaulityReports",params);
    }
  },
  mounted() {},
  created() {
    this.initTime();
  },
  components: {},
  computed: {
    station() {
      return this.$store.state.stationInfo;
    }
  },
  watch: {
    station(value) {
      if (value.length > 0) {
        this.stationInfo = value[0];
         
        this.getTableData()
      }
    }
  },
  destroy() {}
};
</script>

<style scoped lang="less">
.single-station-report-page {
  height: 100%;
  .currency-mode-wrap {
    height: calc(100% - 56px);
  }
  .currency-mode-scroll {
    height: 100%;
  }
  .report-form-title {
    height: 28px;
    line-height: 28px;
    font-size: 16px;
    text-align: center;
    margin-top: 12px;
  }
  .report-form-subtitle {
    text-align: center;
    line-height: 24px;
    height: 24px;
  }
  .report-table-wrap {
    padding: 10px 34px 0;
  }
}
</style>



